.homepage-container{
    
    display:flex;
    flex-direction: column;
    margin:100px 10px 10px 10px;
    background-color: white;
}
.homepage-container .heading{
    margin:10px;
    color: black;
    transition: -webkit-transform 0.5s;
    transition: transform 0.5s;
    transition: transform 0.5s, -webkit-transform 0.5s;
}
.homepage-container .heading:hover{
    -webkit-transform: scaleY(1.5);
            transform: scaleY(1.5);
}
.homepage-container #most-viewed{
    margin-top:30px;
}
.carousel-container{
    display: flex;
    flex-wrap: wrap;
   
    height:250px;
    box-shadow: 0px 10px 10px 0px;
    margin-left:10px;
}
#myCarousel{
    height:250px;
}
.carousel-caption{
 
    bottom:0;
   
    display:flex;
}
.title-class{
    padding-right:10%;
}
.description-class{
    padding-left:40%;
}

html {
  /* scroll-behavior: smooth; */
}


h1 {
  font-family: Arial;
  color: red;
  text-align: center;
}

.wrapper {
  display: grid;
  grid-template-columns: repeat(5,100%);
  overflow:hidden;
  scroll-behavior: smooth;
}
  section {
    width: 100%;
    position: relative;
    display: grid;
    grid-template-columns: repeat(5, auto);
    margin: 20px 0;
  }
   section .item {
      padding: 0 2px;
      transition: 0.5 all;
    }
   section .item:hover { 
      margin: 0 40px;
      -webkit-transform: scale(1.2);
              transform: scale(1.2);
    }
   section a {
      position: absolute;
      color: #fff;
      text-decoration: none;
      font-size: 6em;
      background:rgb(0,0,0);
      width: 80px;
      padding: 20px;
      text-align: center;
      z-index: 1;
     
      
    }
     
   section a:nth-of-type(1) {
      top:0; bottom:0; left:0;
      background: linear-gradient(-90deg, rgba(0,0,0,0) 0%, rgba(0,0,0,1) 100%);
    }
   section a:nth-of-type(2) {
      top:0; bottom:0; right: 0;
      background: linear-gradient(90deg, rgba(0,0,0,0) 0%, rgba(0,0,0,1) 100%);
    }
  
.slider-container{
  font-size:14px;
  color:white;
}
.slider-info{
  margin-top:10px;
  background-color: #b0c5c5;
  
}
